@import '../../scss';

:host {
  display: block;
  width: 100%;

  .item-highlight {
    display: none;
  }

  phn-side-drawer-item {
    margin-top: p-px-to-rem(36px);

    @include pds-media-query-min('m') {
      margin-top: p-px-to-rem(13.6px);
    }

    @include pds-media-query-min('xxl') {
      margin-top: p-px-to-rem(16px);
    }

    &.models {
      margin-top: p-px-to-rem(8px);

      @include pds-media-query-min('xxl') {
        margin-top: p-px-to-rem(16px);
      }
    }

    &:first-of-type {
      margin-top: 0;
    }
  }
}
