import { Component, Event, EventEmitter, Fragment, h, Prop } from '@stencil/core';
import { getSideDrawerItems } from '../../services/menu-items-service';
import { dealerSearchResultsStore } from '../../state/dealer-search-results-store';
import { navContentStore } from '../../state/nav-content-store';
import { navStateStore } from '../../state/nav-state-store';
import { isModelName, isString } from '../../utility/helper';

@Component({
  tag: 'phn-side-drawer',
  styleUrl: 'side-drawer.scss',
  scoped: true
})
export class SideDrawer {
  @Prop() activeRoutingKey!: string;
  @Prop() locale!: string;
  @Event() sideDrawerItemClick!: EventEmitter<string[]>;

  render(): HTMLPhnSideDrawerElement {
    const { loggedInState, unreadMessagesCount, userConsent } = navStateStore.state;

    const sideDrawerItems = getSideDrawerItems(
      navContentStore.state,
      dealerSearchResultsStore.state,
      this.locale,
      loggedInState,
      unreadMessagesCount,
      this.activeRoutingKey,
      userConsent
    );

    return (
      <nav>
        {sideDrawerItems.map(({ routingKeys, ...sideDrawerItem }) => {
          const isActive = routingKeys.includes(this.activeRoutingKey);

          return (
            <Fragment>
              <phn-side-drawer-item
                key={sideDrawerItem.elementId}
                onClick={() => {
                  if (!isString(sideDrawerItem.link) && !isActive) {
                    this.sideDrawerItemClick.emit(routingKeys);
                  }
                }}
                isActive={isActive}
                class={isModelName(sideDrawerItem.text) ? 'models' : ''}
                {...sideDrawerItem}
              />
              {isActive ? <div class={`item-highlight`} /> : null}
            </Fragment>
          );
        })}
      </nav>
    );
  }
}
